.modal__btn_close_line {
	position: relative;
	width: 100%;
	height: 100%;
}

.modal__btn_close_line::after,
.modal__btn_close_line::before {
	content: '';
	position: absolute;
	width: 30px;
	height: 2px;
	border-radius: 2px;
	background-color: #d9d9d9;
	top: 47%;
	right: -4px;
}

.modal__btn_close_line::before {
	-webkit-transform: rotate(45deg);
	transform: rotate(45deg);
}

.modal__btn_close_line::after {
	-webkit-transform: rotate(-45deg);
	transform: rotate(-45deg);
}

